<template>
  <div class="container">
    <div class="studentExams container p-0 mt-5">
      <h3 class="page-title">أمتحانات الطلبة</h3>
      <div v-if="loading" class="center">
        <loading />
      </div>
      <hr />
      <div v-if="students.length == 0" class="center">
        <h5 class="text-center">لا يوجد طلبة في هذه الوحدة</h5>
      </div>
      <div v-if="students.length > 0">
        <div
          class="exam"
          :class="{
            done: student.done,
            toCorrect: !student.done,
            late: student.solution && !student.solution.inTime,
          }"
          @click="
            () => {
              if (!student.solution) return;
              getAnswers(student.solution._id);
            }
          "
          v-for="(student, i) in students"
          :key="i"
        >
          <h4>({{ student.name }})</h4>
          <h4>({{ student.phone }})</h4>
          <div v-if="student.solution" dir="rtl">
            <span> اجابة امتحان رقم</span>
            <span> ({{ student.solution.number }})</span>
            <span> ({{ student.solution.name }})</span>
            <span> ({{ student.solution.unit | section(student.solution.stage) }}) </span>
          </div>
          <h4 v-if="student.solution">
            ({{
              student.solution.fullDegree > 0
                ? student.solution.totalDegree + "/" + student.solution.fullDegree
                : ""
            }})
          </h4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import { eventBus } from "../../../main";
import Solution from "./solution.vue";
export default {
  created() {
    const examId = this.$route.params.examId;
    this.loading = true;
    Axios.get(`/getExamStudentsSolutions/${examId}`).then((res) => {
      this.loading = false;
      this.students = res.data.examStudents;
      console.log(this.students);
    });
    eventBus.$on("delete-solution", (solutionId) => {
      const index = this.students.findIndex((stud) => stud.solution._id == solutionId);
      this.students[index]["solution"] = null;
      this.students[index]["done"] = false;
    });
  },
  data() {
    return {
      loading: false,
      students: [],
    };
  },
  methods: {
    getAnswers(solutionId) {
      Axios.get(`/getUnitExamSolutionById/${solutionId}`).then((res) => {
        eventBus.$emit("open-modal", {
          component: Solution,
          props: { exam: res.data.solution },
        });
      });
    },
  },
};
</script>

