<template>
  <div class="examToCorrect bg-light">
    <hr />
    <div class="header">
      <h3 style="color: #555; font-weight: bold">
        {{ examToCorrect.userId | student("fullname") }}
      </h3>
      <h3>({{ examToCorrect.number }}) أمتحان رقم</h3>
      <!-- <h3>({{exam}}) نموذج</h3> -->
      <h4>{{ examToCorrect.stage | stage }}</h4>
      <h4>
        <strong></strong>({{ examToCorrect.year }}/{{ examToCorrect.year + 1 }}) العام
        الدراسي
      </h4>
      <h2 style="color: red">({{ mark }} / {{ fullMark }})</h2>
    </div>
    <hr />
    <div class="solution">
      <div v-for="(section, s) in examToCorrect.sections" :key="s" class="section">
        <h4 class="aligned" v-if="!section.type.includes('http')">
          {{ section.type }}
        </h4>
        <img
          class="img-fluid aligned"
          v-if="section.type.includes('http')"
          sizes="(min-width: 30em) 30em, 100vw"
          :src="'https' + section.type.split('http')[1]"
        />
        <div
          v-for="(question, q) in section.questions"
          :key="q"
          class="question directed"
        >
          <div class="right aligned">
            <div class="ques aligned" v-if="!question.question.includes('http')">
              {{ "-" + question.question + "؟" }}
            </div>
            <img
              class="ques img-fluid"
              v-if="question.question.includes('http')"
              sizes="(min-width: 30em) 30em, 100vw"
              :src="'https' + question.question.split('http')[1]"
            />
            <div class="answer directed">
              <strong style="color: #7fb68e; font-size: 16px">الاجابة </strong>:
              <div>{{ question.answer }}</div>
            </div>
            <div class="correction directed w-100">
              <input
                class=""
                type="text"
                v-model="question.correction"
                placeholder="correction"
              />
              <!-- <div class=""> :التصحيح</div> -->
            </div>
          </div>
          <div class="degree">
            <integer-plusminus
              @change="calculateMark()"
              v-model="question.degree"
              :min="0"
              :max="question.fullDegree"
              :value="0"
              :increment-aria-label="'increase by 1'"
              :decrement-aria-label="'decrease by 1'"
              :spin-button-aria-label="'example two'"
            ></integer-plusminus>
            <p style="font-size: 1.5rem; color: #5559">({{ question.fullDegree }})</p>
          </div>
        </div>
      </div>
    </div>

    <div v-if="loading" class="center">
      <loading />
    </div>

    <div class="row options">
      <div class="col-md-6">
        <button @click="submitCorrection" type="button" class="btn submit">
          نشر التصحيح
        </button>
      </div>
      <div class="col-md-6">
        <button
          @click="() => removeCorrection(examToCorrect._id)"
          type="button"
          class="btn btn-danger"
        >
          مسح الامتحان للطالب
        </button>
      </div>
    </div>
    <hr />
  </div>
</template>

<script>
import IntegerPlusminus from "../../../components/admin/IntegerPlusMinus";
import axios from "axios";
import { eventBus } from "../../../main";
export default {
  components: {
    IntegerPlusminus,
  },
  created() {
    console.log(this.exam);
  },
  props: ["exam"],
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    examToCorrect() {
      return this.exam;
    },
    fullMark() {
      let mark = 0;
      this.examToCorrect?.sections?.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + ques.fullDegree;
        });
      });
      return mark;
    },
    mark() {
      let mark = 0;
      this.examToCorrect?.sections?.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + +ques.degree;
        });
      });
      return mark;
    },
    adminData() {
      return this.$store.getters.adminData;
    },
  },
  methods: {
    calculateMark() {
      let mark = 0;
      this.examToCorrect.sections.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + +ques.degree;
        });
      });
      this.mark = mark;
    },
    submitCorrection() {
      const correction = {
        ...this.examToCorrect,
        totalDegree: this.mark,
        fullDegree: this.fullMark,
      };
      this.loading = true;
      axios
        .post("/sendUnitExamCorrectionByAdmin/" + this.examToCorrect._id, correction)
        .then((res) => {
          this.loading = false;
          if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
          this.$store.dispatch("writemessage", "تم حفظ التصحيح...");
        });
    },
    async removeCorrection(id) {
      const confirmed = await confirm("هل تؤكد على المسح نهائيا ؟");
      if (!confirmed) return;
      axios.delete("/removeUnitExamCorrection/" + id).then((res) => {
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        eventBus.$emit("close-modal");
        eventBus.$emit("delete-solution", id);
      });
    },
  },
};
</script>

